.search-form {
  border-bottom: 2px solid #000;
  justify-content: center;
  column-gap: 15px;
  margin-top: 50px;
  padding-bottom: 15px;
  display: flex;
}

.container {
  width: 350px;
  height: 50px;
  background-color: #1e272e;
  border-radius: 4rem;
  align-items: center;
  padding: 10px;
  display: flex;
  position: absolute;
  top: 50px;
  left: 500px;
  transform: translate(-50%, -50%);
}

.search__box {
  float: left;
  width: 0;
  height: 4rem;
  color: #f7f7f7;
  opacity: 1;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 2rem;
  outline: none;
  font-size: 1.5rem;
  transition: all .75s ease-in;
  position: relative;
}

.container:hover > .search__box {
  width: 85%;
  padding: 0 1rem;
}

.btn-search {
  color: orange;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid orange;
  border-radius: 15px;
  padding: 5px 8px;
}

.btn-search:hover, .btn-search:focus {
  color: #000;
  background-color: orange;
}

